'use client';

import { useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { NavShell } from '@/app/components/NavShell';

export default function LegacyTaskPage() {
  const params = useParams<{ taskId: string }>();
  const taskId = decodeURIComponent(params.taskId);
  const router = useRouter();

  useEffect(() => {
    // We canâ€™t resolve a task without knowing its board path in Firestore.
    // Send users back to boards.
    const t = setTimeout(() => router.replace('/boards'), 800);
    return () => clearTimeout(t);
  }, [router]);

  return (
    <NavShell title="Task">
      <div className="panel p-4">
        <div className="font-extrabold">This task link is outdated.</div>
        <div className="mt-2 text-sm text-[color:var(--muted)]">
          Tasks are now stored under a board in Firestore, so we need the board context to open them.
        </div>
        <div className="mt-2 text-xs text-[color:var(--muted)]">Task id: {taskId}</div>
        <div className="mt-4">
          <button onClick={() => router.replace('/boards')} className="btn-primary px-4 py-3 rounded-2xl font-extrabold">
            Go to boards
          </button>
        </div>
      </div>
    </NavShell>
  );
}
